html {
    font-size: 10px;
  }
  
* {
    box-sizing: border-box;
  }
  
body {
    margin: 0;
    padding: 0;
    background-color: #fcfbfa;
    color: #212529;
    font-size: 2rem;
  }

.container {
      margin: 0 auto;
      max-width: 700px;
      padding: 40px;
  }

  h1 {
      font-size: 5rem;
      color: #343a40;
  }

  .input {
    font-size: 1.6rem;
    padding: 0.5rem 2rem;
    line-height: 2.8rem;
    border-radius: 5px;
    border: 0.7px solid #ced4da;
    margin-bottom: 1rem;
  }

  .button {
    background-color: #ffd588;
    color: #343a40;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 250ms;
  }
  
  .button:hover {
    background-color: #f7bd51;
  }
  
  select {
    appearance: none;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
    background-repeat: no-repeat, repeat;
    background-position: right .45em top 50%, 0 0;
    background-size: .65em auto, 100%;
    background-color: #98dcfc;
    color: #343a40;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 250ms;
  }

  select:after {
    position: absolute;
    content: " ";
  }

  ul {
      padding: 0;
  }

  .note {
    list-style: none;
    padding: 2rem 4rem;
    margin-bottom: 2rem;
    border-radius: 5px;
    box-shadow: 1px 1px 4px #7f7f7f;
    background-color: #fcf299;
  }

  .crossed {
    list-style: none;
    text-decoration: line-through;
    padding: 2rem 4rem;
    margin-bottom: 2rem;
    border-radius: 5px;
    box-shadow: 1px 1px 4px #7f7f7f;
    background-color: #cad6a8;
  }

  .checkbtn {
    background-color: #b7ce63;
    color: #343a40;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 250ms;
  }
  
  .checkbtn:hover {
    background-color: #8fb339;
  }

  .delbtn {
    background-color: #dfa4a4;
    color: #343a40;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 250ms;
  }

  .delbtn:hover {
    background-color: #f04d4d;
  }